
import React from 'react';

import { Box, Grid } from 'grommet';

import Calendar from '../../calendar/calendar.json'


const Header = ({ children }) => <Box align="center" background="accent-2"> { children } </Box>

const NColumnGrid = ({ month }) => {

  return month && <Grid
  columns={{
    count: 7,
    size: 'auto',
  }}
  
  >
    <Header> Mon </Header>
    <Header> Tue </Header>
    <Header> Wed </Header>
    <Header> Thur </Header>
    <Header> Fri </Header>
    <Header> Sat </Header>
    <Header> Sun </Header>
    {
      Calendar.map((e, i) => {
        return <Box 
          pad="small"
          height="120px" 
          background="brand">  
        {
          e.month === month && <>
          <div>{ e.date } </div>
          <div>{ e.dayPillar }</div>
          <div>{ e.officer }</div>
          </>
        }
        </Box>
      })
    }
  </Grid>
}

export default NColumnGrid